import CustomElement from './CustomElement';
import MostUsedAppsCard from '../components/MostUsedAppsCard';

const ATTRIBUTES = {
  serviceUrl: 'service-url',
  locale: "locale"
};

class MostUsedAppsCardElement extends CustomElement {
  static TAG = 'dw-most-used-apps-card'; // kebab-case

  constructor() {
    super(ATTRIBUTES, MostUsedAppsCardElement);
  }

  get children() {
    const attrs = this.getAllAttributes();
    return <MostUsedAppsCard {...attrs} />
  }

  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }
}

if (!customElements.get(MostUsedAppsCardElement.TAG)) {
  customElements.define(MostUsedAppsCardElement.TAG, MostUsedAppsCardElement);
}