import PropTypes from 'prop-types';
// material
import { styled } from '@mui/material/styles';
import { Box, Link, Card, CardHeader, Stack, Skeleton } from '@mui/material';
import Scrollbar from '../mui-theme/components/Scrollbar';
import ColorPreview from '../mui-theme/components/ColorPreview';
import { useTranslation } from 'react-i18next';
import { useAssetPath } from '../hooks/AssetPath';
import useFetch from '../hooks/FetchAPI';
import { useAuth } from '../hooks/KeycloakContext';
import { useEffect } from 'react';
import { apiMostUsedAppsGet } from '../api/apps';

const ThumbImgStyle = styled('img')(({ theme }) => ({
  width: 48,
  height: 48,
  objectFit: 'cover',
  borderRadius: theme.shape.borderRadiusSm
}));

AppItem.propTypes = {
  app: PropTypes.shape({
    colors: PropTypes.arrayOf(PropTypes.string),
    image: PropTypes.string,
    name: PropTypes.string,
  }),
  pathPrefix: PropTypes.string,
  isLoading: PropTypes.bool
};

function AppItem({ isLoading, app, pathPrefix }) {
  const { name, image } = app;

  return (
    <Stack direction="row" spacing={2} alignItems="center">
      {isLoading ? (
        <Skeleton>
          <ThumbImgStyle alt={name} src={pathPrefix + image} />
        </Skeleton>
      ) : (
        <ThumbImgStyle alt={name} src={pathPrefix + image} />
      )
      }
      <Box sx={{ flexGrow: 1, minWidth: 200 }}>
        {isLoading ? (
          <Skeleton width="50%" variant="circle">
            <Link component={Link} to="#" sx={{ color: 'text.primary', typography: 'subtitle2' }}>
              .
            </Link>
          </Skeleton>
        ) : (
          <Link component={Link} to="#" sx={{ color: 'text.primary', typography: 'subtitle2' }}>
            {name}
          </Link>
        )
        }
      </Box>
      {isLoading ? (
        <>
          <Skeleton animation="wave" variant="circular" width={16} height={16} />
          <Skeleton animation="wave" variant="circular" width={16} height={16} />
          <Skeleton animation="wave" variant="circular" width={16} height={16} />
        </>
      ) : (
        <ColorPreview limit={3} colors={app.colors} sx={{ minWidth: 72, pr: 3 }} />
      )
      }
    </Stack>
  );
}

export default function MostUsedAppsCard({ serviceUrl }) {
  const { t } = useTranslation();
  const pathPrefix = useAssetPath();
  const auth = useAuth();
  const [fetchMostUsedAppsStat, doFetchMostUsedApps] = useFetch(auth, []);

  useEffect(() => {
    doFetchMostUsedApps(() => apiMostUsedAppsGet(serviceUrl));
  }, [auth.authenticated])

  const skeletonApp = [
    {
      id: 1
    },
    {
      id: 2
    },
    {
      id: 3
    },
    {
      id: 4
    },
    {
      id: 5
    },
  ]

  const loadedApp = fetchMostUsedAppsStat.data.length === 0 ? skeletonApp : fetchMostUsedAppsStat.data;

  return (
    <Card className="card">
      <CardHeader title={t('apps.widgetTitle')} />
      <Scrollbar sx={{ maxHeight: 518 }}>
        <Stack spacing={3} sx={{ p: 3, pr: 0 }}>
          {loadedApp.map((app) => (
            <AppItem key={app.id} app={app} pathPrefix={pathPrefix} isLoading={fetchMostUsedAppsStat.isLoading || fetchMostUsedAppsStat.data.length === 0} />
          ))}
        </Stack>
      </Scrollbar>
    </Card>
  );
}
